.home-contact {
  margin-top: 50px;
  align-items: center;
  display: flex;
  flex-direction: column;
  background-color: #101010;
  color: white;
  gap: 1.4rem;
  padding: 5rem 0;
}

.home-contact > p {
  font-size: 3rem;
  font-weight: bold;
  line-height: 1em;
}

.separator {
  width: 14rem;
  height: 3px;
  background-color: white;
}

.home-contact > a {
    font-size: 1.2rem;
    font-weight: bold;
    line-height: 1em;
}

.home-footer {
    background-color: #851213;
    display: flex;
    align-items: center;
    flex-direction: column;
    color: white;
    padding: 3rem 0;
    font-size: 1.2rem;
}