.sphere-view-item {
    position: absolute;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    overflow: hidden;    
    box-shadow: 0 0px 8px 0 rgba(128, 128, 128, 0.2), 0 0px 0px 0 rgba(128, 128, 128, 0.2);
    background-color: rgba(128, 128, 128, 0.1);
    backface-visibility: visible;
    transition: 0.3s ease;
    cursor: pointer;
}

.sphere-view-item-sphere {
    position: absolute;
    transition: 0.5s scale;
}

.sphere-view-item-sphere.hoverable:hover {
    scale:1.2;
}