.button-left, .button-right {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 2px solid #00000022;
  margin-top: 15px;
}

.button-left::after {
  width: 8px;
  height: 8px;
  transform: translate(-25%, -50%) rotate(45deg);
  border: 1px solid #00000022;
  border-width: 0px 0px 2px 2px;
  content:'';
  position: absolute;
  transition: 0.3s;
}

.button-right::after {
    width: 8px;
    height: 8px;
    transform: translate(-75%, -50%) rotate(-135deg);
    border: 1px solid #00000022;
    border-width: 0px 0px 2px 2px;
    content:'';
    position: absolute;
    transition: 0.3s;
}

.button-left:hover::after, .button-right:hover::after {
    border-color: #00000088;
}

.timeline-content h3 {
  font-size: 1.8rem;
  line-height: 2em;
  text-indent: 0.6rem;
  font-weight: bold;
  color: #333333;
}

.timeline-content p {
  font-size: 1rem;
  text-indent: 1.2rem;
  margin-block-end: 0.4em;
  color: #666666;
}

.timeline-block {
  position: absolute;
  transition: 0.4s;
}

.timeline-block.left {
  transform: translateX(-20%);
  opacity: 0;
  visibility: hidden;
}

.timeline-block.right {
  transform: translateX(20%);
  opacity: 0;
  visibility: hidden;
}