.home-intro {
  min-height: 100vh;
  z-index: 100;
  background-image: url(./bg1.png);
  background-repeat: round;
  display: flex;
  flex-direction: column;
}

.home-intro-main {
  width: 100%;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10%;
  margin-top: 80px;
}

.home-intro-main > div:first-child {
  width: 45%;
  margin: 50px 0;
}

.home-intro-main h1 {
  font-size: 3.5rem;
  color: #0070c0;
  font-weight: bold;
}

.home-intro-main h2 {
  font-size: 2.8rem;
  font-weight: bold;
  margin-block-end: 8px;
}

.home-intro-main p {
  font-size: 1rem;
  line-height: 1.8rem;
  color: #666666;
  margin-block-end: 8px;
}

.selfi {
  width: 25%;
  padding: 2px;
  border: 1px solid #cccccc;
}

.selfi > img {
  border: 1px solid #cccccc;
}

@media screen and (max-width: 800px) {
  .selfi {
    display: none;
  }

  .home-intro-main > div:first-child {
    width: 80%;
  }

  .home-intro {
    background-image: url(./bg2.jpg);
    background-repeat:no-repeat;
  }
}

@media screen and (max-width: 500px) {
  .home-intro-main h1 {
    font-size: 2.5rem;
  }

  .home-intro-main h2 {
    font-size: 1.6rem;
  }
  .home-intro {
    min-height:0;
  }
}
