.home-resume {
  max-height: 100vh;
  margin-top: 50px;
}

.home-resume>div:last-child {
    margin: 0 10%;
} 

.page {
  border: 1px solid red;
}

.home-resume .react-pdf__Document {
  width: 100%;
  overflow:auto;
  max-height: 100vh;
}

.home-resume .react-pdf__Document>div {
  display: flex;
  gap: 1.2rem;
  margin: auto;
  width: 100%;
  flex-wrap: wrap;
  width: fit-content;
  align-items: center;
  row-gap:1.2rem;
  background-color: #D4D4D7;
  padding: 0.8rem;
}

.home-resume .react-pdf__Page {
  border: 1px solid #bbbbbb;
  width: fit-content;
  margin: auto;
}