.timeline-bullet {
    width: 12px;
    height: 12px;
    background-color: #ffffff;
    border-radius: 50%;
    border: 2px solid #00000033;
    transition:0.2s;
    z-index: 1;
    position: relative;
    cursor: pointer;
}
.timeline-bullet:hover {
    background: #ffffff;
    border-color:#DFB700;
}

.timeline-bullet.selected, .timeline-bullet.prev:hover {
    border-color: #DFB70088;
    background-color: #DFB700;
}

.timeline-bullet.prev {
    border-color: #DFB700;
}

.timeline-bullet>p{
    color:#000000cc;
    font-size: 0.8rem;
    position: absolute;
    top: -1.4rem;
    transform: translateX(-50%);
    width:160px;
    text-align: center;
    left:50%;
    transition:0.2s;
}

.timeline-bullet.selected>p {
    color: #FFC760;
}

.timeline-bullet>div {
    position: absolute;
    width: 100px;
    height: 50px;
    transform: translate(-50%, 0);
    display: flex;
    align-items: center;
    top: 6px;
}
.timeline-bullet img {
    opacity: 30%;
    transition: 0.3s;
}

.timeline-bullet:hover img, .timeline-bullet.selected img, .timeline-bullet.prev img {
    opacity: 100%;
}