.home-nav-bar {
  width: 100%;
  height: 80px;
  padding: 10px;
  box-shadow: 0 0px 8px 0 rgba(128, 128, 128, 0.2),
    0 0px 0px 0 rgba(128, 128, 128, 0.2);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 24px;
  padding-right: 50px;
  transition: 0.5s;
  z-index: 101;
  background-color: white;
  position:absolute;
}

.home-nav-bar-fixed {
  backdrop-filter: blur(5px);
  position: fixed;
  background-color: transparent;
  top: 0;
  animation: example 0.5s;
}

@keyframes example {
  from {
    top: -80px;
  }
  to {
    top: 0;
  }
}

.home-nav-bar > a {
  font-size: 12pt;
  font-family: "Space Grotesk", sans-serif;
  letter-spacing: 1.5px;
  color: #888888;
  cursor: pointer;
  transition: 0.3s;
  margin-top: auto;
  margin-bottom: auto;
}

.home-nav-bar > a:hover {
  color: #444444;
}

.placeholder {
  width: 100%;
  height: 0;
}
